<template>
  <div>
    <el-dialog :visible.sync="computerDialog">
      <div slot="title" style="color: white; text-align: center">
        Background
      </div>
      <input
        type="file"
        @change="onFileChange"
        style="display: none"
        ref="file"
      />
      <div class="image-preview">
        <img :src="image" alt="" />
      </div>
      <div class="select-image">
        <span class="sl-label">Select Image</span>
        <div class="sl-button" @click="$refs.file.click()">Select</div>
      </div>

      <div class="action-buttons">
        <div class="ngc-button btn-save" @click="computerDialog = false">
          Updated
        </div>
        <div class="ngc-button btn-cancel" @click="computerDialog = false">
          Cancel
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  computed: {
    computerDialog: {
      get() {
        return this.$store.state.backgroundDialogVisibility;
      },
      set(v) {
        this.$store.commit("setBackgroundDialogVisibility", v);
      },
    },
  },
  data() {
    return {
      image: "",
    };
  },
  methods: {
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.createImage(files[0]);
    },
    createImage(file) {
      var reader = new FileReader();
      var vm = this;

      reader.onload = (e) => {
        vm.image = e.target.result;
      };
      reader.readAsDataURL(file);
    },
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/background-modal.scss";
@import "src/assets/scss/new-group.scss";
</style>
