<template>
  <div>
    <el-dialog :visible.sync="deleteComputerDialog">
      <div slot="title" style="color: white; text-align: center">
        Delete Computer
      </div>

      <div class="trash-image">
        <img src="@/assets/image/delete-user-image.png" alt="" />
      </div>

      <div class="description">
        <span>Are you sure to want to delete?</span>
        <span>You will not be able to undo this action</span>
      </div>

      <div class="action-buttons">
        <el-button class="btn-delete" @click="onClickDelete">Delete</el-button>
        <el-button class="btn-cancel" @click="deleteComputerDialog = false"
          >Cancel</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  computed: {
    deleteComputerDialog: {
      get() {
        return this.$store.state.deleteComputerDialogVisibility;
      },
      set(v) {
        this.$store.commit("setDeleteComputerDialogVisibility", v);
      },
    },
  },
  data() {
    return {};
  },
  methods: {
    onClickDelete() {
      this.$store.dispatch("computer/delete", { notify: this.$notify });
      this.deleteComputerDialog = false;
    },
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/delete-user-modal.scss";
</style>
