<template>
  <div style="display: flex;">
    <div class="sidebar">
      <div class="header-logo">
        <img src="@/assets/image/logo.png" alt="" />
      </div>

      <router-link
        class="sidebar-item"
        v-for="(item, index) in sidebarItems"
        :key="`sidebar-item-${index}`"
        :class="{ selected: $route.name == item.label }"
        :to="item.to"
      >
        <img :src="item.icon" alt="" class="sidebar-item-icon" />
        <span class="sidebar-item-label">
          {{ item.label }}
        </span>
      </router-link>
    </div>
    <div style="width: 100%;">
      <div class="header">
        <div class="logout-button" @click="logout">
          Logout
        </div>
      </div>
      <div class="view-container">
        <router-view></router-view>
        <ComputersModal />
        <BackgroundModal />
        <NewUserModal />
        <EditUserModal />
        <DeleteUserModal />
        <NewIconModal />
        <EditIconModal />
        <DeleteIconModal />
        <NewComputerModal />
        <EditComputerModal />
        <DeleteComputerModal />
        <NewMessageModal />
        <EditMessageModal />
        <DeleteMessageModal />
      </div>
    </div>
  </div>
</template>

<script>
import UserIcon from "../assets/image/user_icon.png";
import ComputerIcon from "../assets/image/computer_icon.png";
import MessageIcon from "../assets/image/message_icon.png";
import GroupIcon from "../assets/image/group_icon.png";
import IconsIcon from "../assets/image/icons_icon.png";

import { mapMutations, mapState } from "vuex";

import ComputersModal from "./ComputersModal";
import BackgroundModal from "./BackgroundModal";
import NewUserModal from "./NewUserModal";
import EditUserModal from "./EditUserModal";
import DeleteUserModal from "./DeleteUserModal";

import NewIconModal from "./NewIconModal";
import EditIconModal from "./EditIconModal";
import DeleteIconModal from "./DeleteIconModal";

import NewComputerModal from "./NewComputerModal";
import EditComputerModal from "./EditComputerModal";
import DeleteComputerModal from "./DeleteComputerModal";

import NewMessageModal from "./NewMessageModal";
import EditMessageModal from "./EditMessageModal";
import DeleteMessageModal from "./DeleteMessageModal";

export default {
  data() {
    return {
      sidebarItems: [
        {
          icon: UserIcon,
          label: "Users",
          to: "users",
        },
        {
          icon: ComputerIcon,
          label: "Computers",
          to: "computers",
        },
        {
          icon: MessageIcon,
          label: "Messages",
          to: "message",
        },
        {
          icon: GroupIcon,
          label: "Groups",
          to: "groups",
        },
        {
          icon: IconsIcon,
          label: "Icons",
          to: "icons",
        },
      ],
    };
  },
  components: {
    ComputersModal,
    BackgroundModal,
    NewUserModal,
    EditUserModal,
    DeleteUserModal,
    NewIconModal,
    EditIconModal,
    DeleteIconModal,
    NewComputerModal,
    EditComputerModal,
    DeleteComputerModal,
    NewMessageModal,
    EditMessageModal,
    DeleteMessageModal,
  },
  computed: {
    ...mapState(["selectedSidebar"]),
  },
  methods: {
    ...mapMutations(["setSelectedSidebar", "setComputerDialogVisibility"]),
    logout() {
      this.$router.push("/login");
    },
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/layout.scss";
</style>

<style lang="scss">
.el-dialog__header {
  background: #0f3959 0% 0% no-repeat padding-box;
}
</style>
