<template>
  <div>
    <el-dialog :visible.sync="deleteIconDialog">
      <div slot="title" style="color: white; text-align: center">
        Delete Icon
      </div>

      <div class="trash-image">
        <img src="@/assets/image/delete-user-image.png" alt="" />
      </div>

      <div class="description">
        <span>Are you sure to want to delete?</span>
        <span>You will not be able to undo this action</span>
      </div>

      <div class="action-buttons">
        <el-button class="btn-delete" @click="deleteIconDialog = false"
          >Delete</el-button
        >
        <el-button class="btn-cancel" @click="deleteIconDialog = false"
          >Cancel</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  computed: {
    deleteIconDialog: {
      get() {
        return this.$store.state.deleteIconDialogVisibility;
      },
      set(v) {
        this.$store.commit("setDeleteIconDialogVisibility", v);
      },
    },
  },
  data() {
    return {};
  },
  methods: {},
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/delete-user-modal.scss";
</style>
