<template>
  <div>
    <el-dialog width="500px" :visible.sync="newMessageDialog">
      <div slot="title" style="color: white; text-align: center">
        New Message
      </div>
      <div class="input-group">
        <div class="label">Active Date</div>
        <el-date-picker
          v-model="message.activeDate"
          type="date"
          style="width: 100%;"
        >
        </el-date-picker>
      </div>

      <div class="input-group">
        <span class="label">End Date</span>
        <el-date-picker
          v-model="message.endDate"
          type="date"
          style="width: 100%;"
        ></el-date-picker>
      </div>
      <div class="input-group">
        <span class="label">Status</span>
        <el-select v-model="message.status" style="width: 100%;">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>

      <div class="input-group">
        <span class="label">Message</span>
        <el-input
          v-model="message.content"
          :rows="5"
          type="textarea"
        ></el-input>
      </div>

      <div class="action-buttons">
        <el-button class="btn-create" @click="onClickCreate">Create</el-button>
        <el-button class="btn-cancel" @click="onClickCancel">Cancel</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  computed: {
    newMessageDialog: {
      get() {
        return this.$store.state.newMessageDialogVisibility;
      },
      set(v) {
        this.$store.commit("setNewMessageDialogVisibility", v);
      },
    },
  },
  data() {
    return {
      message: null,
      options: [
        {
          value: true,
          label: "Active",
        },
        {
          value: false,
          label: "Deactivate",
        },
      ],
    };
  },
  methods: {
    onClickCreate() {
      this.$store.dispatch("message/create", {
        message: this.message,
        notify: this.$notify,
      });
      this.initMessageObject();
      this.newMessageDialog = false;
    },
    onClickCancel() {
      this.initMessageObject();
      this.newMessageDialog = false;
    },
    initMessageObject() {
      this.message = {
        activeDate: new Date(),
        endDate: new Date(),
        content: "",
        status: true,
      };
    },
  },
  created() {
    this.initMessageObject();
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/new-user-modal.scss";
</style>
