<template>
  <div>
    <el-dialog :visible.sync="newUserDialog">
      <div slot="title" style="color: white; text-align: center">
        New User
      </div>
      <div class="input-group">
        <span class="label">Username</span>
        <el-input v-model="user.userName"></el-input>
      </div>

      <div class="input-group">
        <span class="label">First Name</span>
        <el-input v-model="user.firstName"></el-input>
      </div>
      <div class="input-group">
        <span class="label">Last Name</span>
        <el-input v-model="user.lastName"></el-input>
      </div>
      <div class="input-group">
        <span class="label">Company Name</span>
        <el-input v-model="user.companyName"></el-input>
      </div>
      <div class="input-group">
        <span class="label">Status</span>
        <el-select v-model="user.status" style="width: 100%;">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>

      <div class="action-buttons">
        <el-button class="btn-create" @click="onClickCreate">Create</el-button>
        <el-button class="btn-cancel" @click="onClickCancel">Cancel</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  computed: {
    newUserDialog: {
      get() {
        return this.$store.state.newUserDialogVisibility;
      },
      set(v) {
        this.$store.commit("setNewUserDialogVisibility", v);
      },
    },
  },
  data() {
    return {
      user: null,
      options: [
        {
          value: true,
          label: "Active",
        },
        {
          value: false,
          label: "Deactivate",
        },
      ],
    };
  },
  methods: {
    onClickCreate() {
      this.$store.dispatch("user/create", {
        user: this.user,
        notify: this.$notify,
      });
      this.initUserObject();
      this.newUserDialog = false;
    },
    onClickCancel() {
      this.initUserObject();
      this.newUserDialog = false;
    },
    initUserObject() {
      this.user = {
        userName: "",
        firstName: "",
        lastName: "",
        companyName: "",
        status: true,
      };
    },
  },
  created() {
    this.initUserObject();
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/new-user-modal.scss";
</style>
