<template>
  <div>
    <el-dialog :visible.sync="computerDialog">
      <div slot="title" style="color: white; text-align: center">
        Assign Computers
      </div>
      <div style="display: flex; justify-content: space-around;">
        <div>
          <div class="subtitle">Active Computers</div>
          <div class="list-area">
            <el-checkbox-group
              v-model="activeCheckList"
              style="display: flex; flex-direction: column"
            >
              <el-checkbox
                v-for="(computer, index) in activeComputers"
                :key="`active-computer-${index}`"
                :label="computer"
              >
              </el-checkbox>
            </el-checkbox-group>
          </div>
        </div>
        <div
          style="display: flex; flex-direction:column; justify-content: center;"
        >
          <img
            class="arrow-image"
            src="@/assets/image/swap_right.png"
            @click="moveToAvailableComputers"
          />
          <img
            class="arrow-image"
            style="margin-top: 10px;"
            src="@/assets/image/swap_left.png"
            @click="moveToActiveComputers"
          />
        </div>
        <!-- <el-button
          icon="el-icon-d-arrow-left"
          @click="updateActiveComputers"
          round
        ></el-button> -->
        <div>
          <div class="subtitle">Available Computers</div>

          <div class="list-area">
            <el-checkbox-group
              v-model="availableCheckList"
              style="display: flex; flex-direction: column"
            >
              <el-checkbox
                v-for="(computer, index) in availableComputers"
                :key="`available-computer-${index}`"
                :label="computer"
              ></el-checkbox>
            </el-checkbox-group>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <div class="ngc-button btn-save" @click="computerDialog = false">
          Save
        </div>
        <div class="ngc-button btn-cancel" @click="computerDialog = false">
          Cancel
        </div>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  computed: {
    computerDialog: {
      get() {
        return this.$store.state.computerDialogVisibility;
      },
      set(v) {
        this.$store.commit("setComputerDialogVisibility", v);
      },
    },
  },
  data() {
    return {
      availableComputers: [
        "Computer A",
        "Computer B",
        "Computer C",
        "Computer D",
        "Computer E",
      ],
      activeComputers: [],
      availableCheckList: [],
      activeCheckList: [],
    };
  },
  methods: {
    moveToActiveComputers() {
      let newAvailableComputers = this.availableComputers.filter((computer) => {
        if (this.availableCheckList.indexOf(computer) > -1) {
          this.activeComputers.push(computer);
          return false;
        }
        return true;
      });

      this.availableComputers = newAvailableComputers;
      this.availableCheckList = [];
    },
    moveToAvailableComputers() {
      let newActiveComputers = this.activeComputers.filter((computer) => {
        if (this.activeCheckList.indexOf(computer) > -1) {
          this.availableComputers.push(computer);
          return false;
        }
        return true;
      });

      this.activeComputers = newActiveComputers;
      this.activeCheckList = [];
    },
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/computers-modal.scss";
@import "src/assets/scss/new-group.scss";
</style>
