<template>
  <div>
    <el-dialog :visible.sync="editMessageDialog" v-if="message">
      <div slot="title" style="color: white; text-align: center">
        Edit Message
      </div>
      <div class="input-group">
        <div class="label">Active Date</div>
        <el-date-picker
          v-model="message.activeDate"
          type="date"
          style="width: 100%;"
        >
        </el-date-picker>
      </div>

      <div class="input-group">
        <span class="label">End Date</span>
        <el-date-picker
          v-model="message.endDate"
          type="date"
          style="width: 100%;"
        ></el-date-picker>
      </div>
      <div class="input-group">
        <span class="label">Status</span>
        <el-select v-model="message.status" style="width: 100%;">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>

      <div class="input-group">
        <span class="label">Message</span>
        <el-input
          v-model="message.content"
          :rows="5"
          type="textarea"
        ></el-input>
      </div>

      <div class="action-buttons">
        <el-button class="btn-create" @click="onClickUpdate">Update</el-button>
        <el-button class="btn-delete" @click="openDeleteMessageDialog"
          >Delete</el-button
        >
        <el-button class="btn-cancel" @click="onClickCancel">Cancel</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    editMessageDialog: {
      get() {
        return this.$store.state.editMessageDialogVisibility;
      },
      set(v) {
        this.$store.commit("setEditMessageDialogVisibility", v);
      },
    },
    ...mapState("message", ["updateMessage"]),
  },
  data() {
    return {
      message: null,
      options: [
        {
          value: true,
          label: "Active",
        },
        {
          value: false,
          label: "Deactivate",
        },
      ],
    };
  },
  methods: {
    openDeleteMessageDialog() {
      this.$store.commit("setEditMessageDialogVisibility", false);
      this.$store.commit("setDeleteMessageDialogVisibility", true);
    },
    onClickUpdate() {
      this.$store.dispatch("message/update", {
        message: this.message,
        notify: this.$notify,
      });
      this.editMessageDialog = false;
    },
    onClickCancel() {
      this.editMessageDialog = false;
    },
  },
  watch: {
    editMessageDialog() {
      this.message = { ...this.updateMessage };
    },
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/new-user-modal.scss";
</style>
