<template>
  <div>
    <el-dialog :visible.sync="editUserDialog" v-if="user">
      <div slot="title" style="color: white; text-align: center">
        Edit User
      </div>
      <div class="input-group">
        <span class="label">Username</span>
        <el-input v-model="user.userName"></el-input>
      </div>

      <div class="input-group">
        <span class="label">First Name</span>
        <el-input v-model="user.firstName"></el-input>
      </div>
      <div class="input-group">
        <span class="label">Last Name</span>
        <el-input v-model="user.lastName"></el-input>
      </div>
      <div class="input-group">
        <span class="label">Company Name</span>
        <el-input v-model="user.companyName"></el-input>
      </div>
      <div class="input-group">
        <span class="label">Status</span>
        <el-select v-model="user.status" style="width: 100%;">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>

      <div class="action-buttons">
        <el-button class="btn-create" @click="onClickUpdate">Update</el-button>
        <el-button class="btn-delete" @click="openDeleteUserDialog"
          >Delete</el-button
        >
        <el-button class="btn-cancel" @click="onClickCancel">Cancel</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    editUserDialog: {
      get() {
        return this.$store.state.editUserDialogVisibility;
      },
      set(v) {
        this.$store.commit("setEditUserDialogVisibility", v);
      },
    },
    ...mapState("user", ["updateUser"]),
  },
  data() {
    return {
      user: null,
      options: [
        {
          value: true,
          label: "Active",
        },
        {
          value: false,
          label: "Deactivate",
        },
      ],
    };
  },
  methods: {
    openDeleteUserDialog() {
      this.$store.commit("setEditUserDialogVisibility", false);
      this.$store.commit("setDeleteUserDialogVisibility", true);
    },
    onClickUpdate() {
      this.$store.dispatch("user/update", {
        user: this.user,
        notify: this.$notify,
      });
      this.editUserDialog = false;
    },
    onClickCancel() {
      this.editUserDialog = false;
    },
  },
  watch: {
    editUserDialog() {
      this.user = { ...this.updateUser };
    },
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/new-user-modal.scss";
</style>
