<template>
  <div>
    <el-dialog :visible.sync="newIconDialogVisibility">
      <div slot="title" style="color: white; text-align: center">
        New Icon
      </div>
      <div class="input-group">
        <span class="label">Application Name</span>
        <el-input v-model="icon.applicationName"></el-input>
      </div>
      <div class="input-group">
        <span class="label">Status</span>
        <el-select v-model="icon.status" style="width: 100%;">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      <div class="input-group">
        <span class="label">File Path</span>
        <el-input v-model="icon.filePath"></el-input>
      </div>

      <div class="input-group">
        <input type="file" style="display: none" ref="file" />
        <div class="label">Select Image</div>
        <el-button
          style="width: 150px; background: #DEDEDE; border-color: #404040"
          @click="$refs.file.click()"
          >Select</el-button
        >
      </div>

      <div class="action-buttons">
        <el-button class="btn-create" @click="newIconDialogVisibility = false"
          >Create</el-button
        >
        <el-button class="btn-cancel" @click="newIconDialogVisibility = false"
          >Cancel</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  computed: {
    newIconDialogVisibility: {
      get() {
        return this.$store.state.newIconDialogVisibility;
      },
      set(v) {
        this.$store.commit("setNewIconDialogVisibility", v);
      },
    },
  },
  data() {
    return {
      icon: {
        applicationName: "",
        filePath: "",
        status: "active",
      },
      options: [
        {
          value: "active",
          label: "Active",
        },
        {
          value: "deactive",
          label: "Deactivate",
        },
      ],
    };
  },
  methods: {
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.createImage(files[0]);
    },
    createImage(file) {
      var reader = new FileReader();
      var vm = this;

      reader.onload = (e) => {
        vm.image = e.target.result;
      };
      reader.readAsDataURL(file);
    },
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/new-user-modal.scss";
@import "src/assets/scss/background-modal.scss";
</style>
