<template>
  <div>
    <el-dialog :visible.sync="editComputerDialog" v-if="computer">
      <div slot="title" style="color: white; text-align: center">
        Edit Computer
      </div>
      <div class="input-group">
        <span class="label">Computer ID</span>
        <el-input v-model="computer.computerId"></el-input>
      </div>

      <div class="input-group">
        <span class="label">Computer Name</span>
        <el-input v-model="computer.computerName"></el-input>
      </div>
      <div class="input-group">
        <span class="label">Group</span>
        <el-input v-model="computer.group"></el-input>
      </div>
      <div class="input-group">
        <span class="label">Status</span>
        <el-select v-model="computer.status" style="width: 100%;">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      <div class="input-group">
        <span class="label">Computer Key</span>
        <el-input v-model="computer.computerKey"></el-input>
      </div>
      <div class="input-group">
        <span class="label">End Point Key</span>
        <el-input v-model="computer.endPointKey"></el-input>
      </div>

      <div class="action-buttons">
        <el-button class="btn-create" @click="onClickUpdate">Update</el-button>
        <el-button class="btn-delete" @click="openDeleteComputerDialog"
          >Delete</el-button
        >
        <el-button class="btn-cancel" @click="onClickCancel">Cancel</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    editComputerDialog: {
      get() {
        return this.$store.state.editComputerDialogVisibility;
      },
      set(v) {
        this.$store.commit("setEditComputerDialogVisibility", v);
      },
    },
    ...mapState("computer", ["updateComputer"]),
  },
  data() {
    return {
      computer: null,
      options: [
        {
          value: true,
          label: "Active",
        },
        {
          value: false,
          label: "Deactivate",
        },
      ],
    };
  },
  methods: {
    openDeleteComputerDialog() {
      this.editComputerDialog = false;
      this.$store.commit("setDeleteComputerDialogVisibility", true);
    },
    onClickUpdate() {
      this.$store.dispatch("computer/update", {
        computer: this.computer,
        notify: this.$notify,
      });
      this.editComputerDialog = false;
    },
    onClickCancel() {
      this.editComputerDialog = false;
    },
  },
  watch: {
    editComputerDialog() {
      this.computer = { ...this.updateComputer };
    },
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/new-user-modal.scss";
</style>
