<template>
  <div>
    <el-dialog :visible.sync="newComputerDialog">
      <div slot="title" style="color: white; text-align: center">
        New Computer
      </div>
      <div class="input-group">
        <span class="label">Computer ID</span>
        <el-input v-model="computer.computerId"></el-input>
      </div>

      <div class="input-group">
        <span class="label">Computer Name</span>
        <el-input v-model="computer.computerName"></el-input>
      </div>
      <div class="input-group">
        <span class="label">Group</span>
        <el-input v-model="computer.group"></el-input>
      </div>
      <div class="input-group">
        <span class="label">Status</span>
        <el-select v-model="computer.status" style="width: 100%;">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      <div class="input-group">
        <span class="label">Computer Key</span>
        <el-input v-model="computer.computerKey"></el-input>
      </div>
      <div class="input-group">
        <span class="label">End Point Key</span>
        <el-input v-model="computer.endPointKey"></el-input>
      </div>

      <div class="action-buttons">
        <el-button class="btn-create" @click="onClickCreate">Create</el-button>
        <el-button class="btn-cancel" @click="onClickCancel">Cancel</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  computed: {
    newComputerDialog: {
      get() {
        return this.$store.state.newComputerDialogVisibility;
      },
      set(v) {
        this.$store.commit("setNewComputerDialogVisibility", v);
      },
    },
  },
  data() {
    return {
      computer: null,
      options: [
        {
          value: true,
          label: "Active",
        },
        {
          value: false,
          label: "Deactivate",
        },
      ],
    };
  },
  methods: {
    onClickCreate() {
      this.$store.dispatch("computer/create", {
        computer: this.computer,
        notify: this.$notify,
      });
      this.initComputerObject();
      this.newComputerDialog = false;
    },
    onClickCancel() {
      this.initComputerObject();
      this.newComputerDialog = false;
    },
    initComputerObject() {
      this.computer = {
        computerName: "",
        firstName: "",
        lastName: "",
        companyName: "",
        status: true,
      };
    },
  },
  created() {
    this.initComputerObject();
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/new-user-modal.scss";
</style>
